@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500;700&display=swap');

$primary-color: #393E46;
$secondary-color: #EEEEEE;
$darker-color: #222831;
$accent-color: #00ADB5;

body {
    margin: 0;
    padding-top: 2rem;
    background-color: $primary-color;
    color: $secondary-color;
    font-family: 'Ubuntu', sans-serif;
    letter-spacing: 3px;
}

#container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#profile-photo {
    width: 25rem;
    height: 25rem;
    border-radius: 100%;
    margin-bottom: 1rem;
    box-shadow: 0px 0px 80px 0px rgba(0, 0, 0, 0.75);
}

#title {
    background-color: $accent-color;
    padding: 1%;
    font-size: 5rem;
    font-weight: 700;
    margin: 2rem 0 2rem 0;
}

#description {
    font-weight: 300;
    font-size: 3rem;
    margin: 0 0 2rem 0;
}

.contact-box {
    background-color: $darker-color;
    width: 40vw;
    padding: 1%;
    margin-bottom: 1rem;
}

.contact-icon {
    width: 3rem;
    color: $secondary-color;
    fill: $secondary-color;
}

#form-div {
    background-color: $darker-color;
    padding: 1%;
}

input {
    padding: 0.5%;
    font-size: 1.5rem;
    background-color: $secondary-color;
}

button {
    padding: 0.5%;
    font-size: 1.5rem;
}